<template>
  <button class="button_settigns__calendar-mode" @click="switchMode()">{{buttonSwitchModeTitle}}</button>
</template>

<style>
  .button_settigns__calendar-mode {
    background: none !important;
    border: 0 !important;
    color: #16407d !important;
    font-size: 1em !important;
    text-decoration: underline !important;
    text-align: left !important;
    padding:0 !important;
    height:auto !important;
    text-transform: none !important;
    font-weight: normal !important;
    width: auto;
  }
</style>

<script>
  export default {
    methods: {
      switchMode: function() {
        this.$store.dispatch('switchMode')
      }
    },
    computed: {
      compactMode () {
        return this.t('Compact Mode')
      },
      normalMode () {
        return this.t('Normal Mode')
      },
      buttonSwitchModeTitle: function(){
        return this.$store.state.display_mode_compact ? this.normalMode : this.compactMode
      }
    }
  }
</script>



