<template>
  <div>
    <input type="checkbox" name="toggle-calendar" id="toggleCalendar" class="filter-toggle-checkbox">
    <label for="toggleCalendar"><i class="icon icon-calendar"></i></label>
    <div class="calendar-view">
      <div class="calendar-wrapper">
        <calendar></calendar>
      </div>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  #toggleCalendar:checked {
    display: none;
  }
  #toggleCalendar:checked + label + .calendar-view {
    display: block;
  }
  .filter-toggle-checkbox {
    display: none;
  }
  #toggleCalendar:checked + label > i.icon-calendar:before {
    content: '\E802';
  }
  #toggleCalendar:checked + label > .icon.icon-calendar {
    border-color: transparent;
    background: transparent;
  }

  .calendar-view {
    width:100%;
    height: 100%;
    background: #fff;
    top:0;
    display: none;
    z-index: 899;
    position: absolute;
  }

  .icon-calendar {
    position: relative;
    z-index:900;
  }

  .calendar-wrapper {
    width: auto;
    transition: all .3s ease;
    text-align: center;
    border: none;
    margin: auto;
    padding: 0;
    background: #fff;
    overflow: hidden;
  }

  .calendar-wrapper .switch button {
    background: none;
    border: 0;
    color: #16407d !important;
    font-size: 1.3rem;
    text-decoration: underline;
    float: left;
  }

  @media (--timeline-small) {
    .calendar-wrapper {
      max-width: 80%;
      min-width: 300px;
    }
    #toggleCalendar:checked + label > .icon.icon-calendar {
      position: absolute;
      top:0;
      right:0;
      left: auto;
    }
  }

  @media (--timeline-large) {
    .calendar-view {
      top:1rem;
      height:auto;
    }
    .calendar-wrapper {
      padding: 1rem 2rem 1rem;
      border: 2px solid #16407d;
      width: 35rem;
    }
    #toggleCalendar:checked + label > .icon.icon-calendar {
      left:31rem;
    }
  }
</style>

<script>
  import Calendar from './Calendar'

  export default {
    components: {
      Calendar
    }
  }
</script>




